      case '晒收入奖励设置':
        this.dialog7=true;
        break;
      case '周签到奖励设置':
        this.dialog8=true;
        break;
      case '绑定第三方账号奖励设置':
        this.dialog9=true;
        break;
      case '徒弟发布奖励设置':
        this.dialog10=true;
        break;
      case '交通币兑换设置':
        this.dialog11=true;
        break;
      case '提现设置':
        this.dialog12=true;
        break;
      }
    },
    
  }
}
</script>
<style>
  .travel_coin .el-dialog__body{
    padding:20px 20px 0;
  }
</style>




